import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { RouteComponentProps } from 'react-router';
// import { Link } from 'react-router-dom';
import * as fs from 'fs-extra';
import { FormControl, Form, FormGroup, ControlLabel, Button, Checkbox, Alert, Glyphicon, Table, Panel, ProgressBar } from 'react-bootstrap';
import { pick, Dictionary } from 'lodash';
import '!style-loader!css-loader?sourceMap!bootstrap/dist/css/bootstrap.css';
// import '!style-loader!css-loader?sourceMap!glyphicons-only-bootstrap/css/bootstrap.min.css';
import * as ActionWorker from './action.worker';

declare type ConstructorOf<T> = new (...args: any[]) => T;

require('./Main.scss');

export interface IProps extends RouteComponentProps<any> {
  increment(): void,
  incrementIfOdd(): void,
  incrementAsync(): void,
  decrement(): void,
  counter: number
}

export interface IState {
  from: string;
  to: string;
  where: string;
  occurrencesCount?: number;
  filesCount?: number;
  work?: boolean;
  regex?: boolean;
  matches: { file: string, content: string }[];
  action?: 'find' | 'replace';
  oldContent?: Dictionary<string>;
  progress?: number;
}

export class Main extends React.Component<IProps, IState> {
  __worker?: Worker;
  __matchesContainerEl: Panel | null = null;

  render() {
    // const { /* increment, incrementIfOdd, incrementAsync, decrement, counter */ } = this.props;
    const { from, to, where, occurrencesCount, filesCount, work, regex, matches, action, oldContent, progress } = this.state;

    const progressContent = occurrencesCount === undefined ? undefined :
      <div>
        <strong>{occurrencesCount}</strong> {action === 'find' ? 'occurrences' : 'replaces'} in <strong>{filesCount}</strong> files
        {!work && action === 'replace' && occurrencesCount > 0 &&
          // todo cancelling..
          (oldContent ?
            <a
              href="javascript:void(0)"
              style={{ display: 'inline-block', marginLeft: 10, color: '#337ab7', textDecoration: 'underline', fontWeight: 'bold' }}
              onClick={this.__cancel}
            >cancel</a> :
            <span style={{ display: 'inline-block', marginLeft: 10, color: '#337ab7' }}>canceled</span>)}
      </div>;

    return (
      <Form style={{ padding: 10 }}>
        <Checkbox
          checked={!!regex}
          onChange={x => this.__setState({ regex: (x.target as any).checked })}
        > RegEx</Checkbox>

        <FormGroup>
          <ControlLabel>From</ControlLabel>
          <FormControl
            value={from}
            onChange={x => this.__setState({ from: (x.target as any).value })}
            componentClass="textarea"
          ></FormControl>
        </FormGroup>

        <FormGroup>
          <ControlLabel>To</ControlLabel>
          <FormControl
            value={to}
            onChange={x => this.__setState({ to: (x.target as any).value })}
            componentClass="textarea"
          ></FormControl>
        </FormGroup>

        <FormGroup>
          <ControlLabel>Where</ControlLabel>
          <FormControl
            value={where}
            onChange={x => this.__setState({ where: (x.target as any).value })}
          ></FormControl>
        </FormGroup>

        <FormGroup>
          {work ?
            <Button bsStyle="warning" onClick={this.__stopAction}><Glyphicon glyph="ban-circle" /> Cancel</Button> :
            <>
              <Button
                bsStyle="primary"
                onClick={() => this.__action(false)}
                disabled={!from}
              ><Glyphicon glyph="search" /> Find</Button>{' '}
              <Button
                bsStyle="success"
                onClick={() => this.__action(true)}
                disabled={!from}
              ><Glyphicon glyph="file" /> Replace</Button>{' '}
            </>
          }
        </FormGroup>

        {progressContent &&
          <FormGroup>
            {work ?
              <ProgressBar className="action-progress" active min={0} max={1} now={progress || 0} label={progressContent} /> :
              <Alert bsStyle="success">{progressContent}</Alert>}
          </FormGroup>}

        {!!matches.length &&
          <Panel style={{ height: 200, overflow: 'auto' }} ref={x => this.__matchesContainerEl = x}>
            <Panel.Body style={{ padding: 0 }}>
              <Table style={{ marginBottom: 0, marginTop: -1 }}>
                <tbody>
                  {matches.map(({ file, content }, i) =>
                    <tr key={i}><td>
                      <div><strong>{file}</strong></div>
                      <div style={{ fontSize: 'smaller' }}>{content.split('\n').map((x, i) => <span key={i}>{x}<br /></span>)}</div>
                    </td></tr>)}
                </tbody>
              </Table>
            </Panel.Body>
          </Panel>}
      </Form>
    );
  }

  constructor(props: Main['props']) {
    super(props);
    this.state = {
      from: '',
      to: '',
      where: '',
      matches: []
    };
    this.state = { ...this.state, ...JSON.parse(localStorage.getItem('state') || '{}') };
  }

  __action = async (replace: boolean) => {
    const worker = this.__worker = new (ActionWorker as ConstructorOf<Worker>)();
    const listener = (e: MessageEvent) => {
      const data = JSON.parse(e.data as string) as ActionWorker.IWorkerResponse;
      this.__setState(data.state);
    };
    worker.addEventListener('message', listener);

    worker.postMessage(JSON.stringify({
      type: 'work',
      replace,
      state: this.state
    } as ActionWorker.IWorkerData));
  }

  __cancel = async () => {
    const { oldContent } = this.state;

    if (oldContent) {
      // todo if there is no file
      await Promise.all(Object.keys(oldContent).map(file => fs.writeFile(file, oldContent[file])));
    }

    this.__setState({ oldContent: undefined });
  };

  __setState: Main['setState'] = (state, cb) => {
    if (state && typeof state === 'object') {
      const saveToStorage: (keyof IState)[] = ['where', 'from', 'to'];
      if (saveToStorage.some(key => key in state && state[key] !== this.state[key])) {
        localStorage.setItem('state', JSON.stringify(pick({ ...this.state, ...(state as any) }, saveToStorage)));
      }
    }
    this.setState(state, () => {
      if (cb) cb();
      if (state && typeof state === 'object' && state.matches) {
        this.__scrollMatches();
      }
    });
  }

  __scrollMatches = () => {
    if (this.__matchesContainerEl) {
      ReactDOM.findDOMNode(this.__matchesContainerEl).scrollTop = Number.MAX_SAFE_INTEGER;
    }
  }

  __stopAction = () => {
    this.__worker!.postMessage(JSON.stringify({
      type: 'stop'
    } as ActionWorker.IWorkerData));
  }
}

export default Main;



